const user = {
    setUserInfo : ({commit}, userInfo) => {
        return commit('SET_USERINFO', userInfo)
    },
    setUserPic : ({commit}, userPic) => {
        return commit('SET_USERPIC', userPic)
    },
    setUserId : ({commit}, userId) => {
        return commit('SET_USERID', userId)
    },
    setUserCover : ({commit}, userCover) => {
        return commit('SET_USERCOVER', userCover)
    },
    setUserCollectonProducts : ({commit}, userCollectonProducts) => {
        return commit('SET_USERCOLLECTONPRODUCTS', userCollectonProducts)
    },
    setUserGender : ({commit}, userGender) => {
        return commit('SET_USERGENDER', userGender)
    },
    setUserDesc : ({commit}, userDesc) => {
        return commit('SET_USERDESC', userDesc)
    }
}

export default user
