const user = {
    state: {
        userInfo: '',
        userPic: '',
        userId: '',
        userCover:'',
        userCollectonProducts : [],
        userGender: '',
        userDesc: ''
    },
    mutations: {
        'SET_USERINFO': (state, userInfo) => {
            state.userInfo = userInfo
        },
        'SET_USERPIC': (state, userPic) => {
            state.userPic = userPic
        },
        'SET_USERID': (state, userId) => {
            state.userId = userId
        },
        'SET_USERCOVER': (state, userCover) => {
            state.userCover = userCover
        },
        'SET_USERCOLLECTONPRODUCTS': (state, userCollectonProducts) => {
            state.userCollectonProducts = userCollectonProducts
        },
        'SET_USERGENDER': (state, userGender) => {
            state.userGender = userGender
        },
        'SET_USERDESC': (state, userDesc) => {
            state.userDesc = userDesc
        }
    }
}

export default user
