import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    // mode: 'hash',
    routes: [
        {
            path: '/',
            name: 'goodslist',
            component: () => import('./views/GoodsList'),
        },
        {
            path: '/cart',
            name: 'showMyCart',
            component: () => import('./views/Cart'),
        },
        {
            path: '/changeUserPic',
            name: 'changeUserPic',
            component: () => import('./views/changeUserPic')
        },
        {
            path: '/orderConfirm/:id',
            component: () => import('./views/OrderConfirm')
        },
        {
            path: '/address',
            name: 'address',
            component: () => import('./views/Address.vue')
        },
        {
            path: '/orderSuccess/:orderId/:orderTotal',
            name: 'ordersuccess',
            component: () => import('./views/OrderSuccess.vue')
        },
        {
            path: '/testModal',
            name: 'testModal',
            component: () => import('./views/TestModal.vue')
        },
        {
            path: '*',
            name: '404',
            component: () => import('./views/404')
        }
    ],
    scrollBehavior (to, from, savedPosition) {
        return { x: 0, y: 0 }
    }

})
